$flexy-block-flash-types: (
    success: #27ae60,
    info: #2980b9,
    warning: #d35400,
    error: #c0392b
  )
  !default;
$flexy-block-flash-border-radius: $flexy-theme-border-radius !default;
$flexy-block-flash-font-size: 0.9em !default;
$flexy-block-flash-styles: (
    margin-bottom: 0.5em,
    user-select: none,
    cursor: pointer,
    color: #fff,
    border-radius: $flexy-block-flash-border-radius,
    box-sizing: border-box,
    padding: 0.5em 1em,
    font-size: $flexy-block-flash-font-size
  )
  !default;

.b-flash {
  @each $attr, $value in $flexy-block-flash-styles {
    #{$attr}: #{$value};
  }

  @each $prefix, $color in $flexy-block-flash-types {
    &_#{$prefix} {
      background: {
        color: $color;
      }
    }
  }
}
