$flexy-theme-height: 32px !default;

$flexy-theme-sizes: (
    xsmall: 0.5em,
    small: 0.707em,
    medium: 1em,
    large: 1.414em,
    xlarge: 1.999em,
    xxlarge: 2.827em
  )
  !default;

$flexy-theme-colors: (
    default: #338fff,
    gray: #cae2ef,
    error: #dd162d,
    success: #4caf50,
    primary: #2196f3
  )
  !default;
$flexy-theme-border-radius: 2px !default;
$flexy-theme-button-padding: 0 10px !default;
$flexy-theme-border-size: 1px !default;
$flexy-theme-group-offset: 5px !default;
$flexy-theme-blockquote-border-size: 3px !default;

